h1 {
  font-family: "Times New Roman", Times, serif;
  font-size: larger;
  color: #502999;
  text-align: center;
}
.searchbox {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 20%;
  font-size: 1.5em;
  background: linear-gradient(21deg, #1d044b, #ff4e17);
  padding: 5px;
  outline: none;
  border-radius: 9999em;
}
.cards {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 20%;
}
.cardsbox {
}

.cards:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
